.home {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	height: 100%;
	div.hide_block {
		&:first-child {
			width: 130px;
		}
		&:last-child {
			width: 30.8%;
		}
	}
	.main_panel {
		text-transform: uppercase;
		color: black;
		font-family: "Roboto-Medium";
		font-size: 14px;
		display: -ms-flexbox;
		display: flex;
		width: 47%;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
		>div {
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: center;
			    justify-content: center;
		}
		div {
			+ {
				div {
					margin-left: 10.5%;
				}
			}
		}
		span {
			display: inline-block;
			background-color: #f57c00;
			padding: 5px 8px;
			margin-left: 15px;
			color: white;
			font-size: 12px;
			font-family: "Roboto-Regular";
			border-radius: 3px;
		}
		p {
			text-align: center;
		}
	}
}
.m_home {
	-ms-flex-pack: center;
	    justify-content: center;
	display: none;
	.main_panel_m {
		font-size: 14px;
		text-transform: uppercase;
		color: black;
		font-family: "Roboto-Medium";
		-ms-flex-direction: column;
		    flex-direction: column;
		height: -webkit-max-content;
		height: -moz-max-content;
		height: max-content;
		>div {
			display: -ms-flexbox;
			display: flex;
			height: 48px;
			-ms-flex-align: center;
			    align-items: center;
		}
		span {
			display: inline-block;
			background-color: #f57c00;
			padding: 5px 8px;
			margin-left: 0;
			margin-right: 15px;
			color: white;
			font-size: 12px;
			font-family: "Roboto-Regular";
			border-radius: 3px;
		}
	}
}


@media (max-width: 1024px) {
	.home {
		div.hide_block {
			&:first-child {
				display: none;
			}
			&:last-child {
				display: none;
			}
		}
		.main_panel {
			width: 100%;
			div {
				+ {
					div {
						margin-left: 3%;
					}
				}
			}
		}
	}
}

@media (max-width: 700px) {


.home {
	display: none;
}
.m_home {
	display: block;
	max-height: 48px;
	-webkit-transition: max-height 0.3s;
	-o-transition: max-height 0.3s;
	transition: max-height 0.3s;
	p {
		-ms-flex-positive: 1;
		    flex-grow: 1;
	}
	.drop_img {
		-webkit-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
	}
	.drop_img.down {
		-webkit-transform: rotate(180deg);
		    -ms-transform: rotate(180deg);
		        transform: rotate(180deg);
	}
}
.m_home.drop {
	max-height: 144px;
}
}


