.shadow {
	-webkit-box-shadow: 1px 2px 7px rgba(0, 0, 0, 0.24);
	        box-shadow: 1px 2px 7px rgba(0, 0, 0, 0.24);
	cursor: pointer;
	-webkit-transition: -webkit-box-shadow 0.3s;
	transition: -webkit-box-shadow 0.3s;
	-o-transition: box-shadow 0.3s;
	transition: box-shadow 0.3s;
	transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;
	padding-top: 10px;
	padding-bottom: 16px;
	&:hover {
		-webkit-box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.16);
		        box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.16);
	}
}
.shadow.card {
	&:hover {
		.desc {
			bottom: -64px;
		}
		.logo {
			height: 115px;
		}
		p {
			margin-top: 18px;
		}
	}
}
.shadow.card {
	text-align: center;
	min-height: 246px;
	padding-bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	overflow: hidden;
	.logo {
		width: 100%;
		height: 131px;
		-webkit-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
		img {
			height: inherit;
		}
	}
	.desc {
		padding: 10px 0;
		width: 100%;
		position: relative;
		border-top: 1px solid #e0e0e0;
		bottom: -105px;
		-webkit-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
		p {
			margin: 0;
			font-size: 13px;
			font-family: "Roboto-Regular";
			font-weight: 300;
			color: #ccc;
			a {
				color: #303f9f;
				font-size: 14px;
			}
		}
	}
	p {
		font-family: "Roboto-Medium";
		font-size: 16px;
		-webkit-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s;
		margin-top: 15px;
	}
}

@media (max-width: 903px) {
	.shadow.card {
		min-height: 266px;
		.desc {
			bottom: -125px;
		}
	}
}

@media (max-width: 767px) {
	.shadow.card {
		min-height: 246px;
		.desc {
			bottom: -105px;
		}
	}
}



