.panel_info {
	-webkit-box-shadow: 2px 1px 4px #d5d5d5;
	        box-shadow: 2px 1px 4px #d5d5d5;
	padding: 0 24px;
	height: 48px;
}
@media (max-width: 700px) {
	.panel_info {
			height: 100%;
			overflow: hidden;
			position: absolute;
			height: auto;
			width: 100%;
			z-index: 3;
			background-color: white;
		}

}


